import * as React from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Button,
  Avatar,
  Menu,
  MenuItem,
  Divider,
  FormControlLabel,
  Switch,
  Grid,
} from "@mui/material";
import { KeyboardArrowDown as KeyboardArrowDownIcon } from "@mui/icons-material";
import { Person as PersonIcon } from "@mui/icons-material";
import { green } from "@mui/material/colors";
import Auth from "../../utils/auth";

const Header = ({ theme, themeToggle }) => {
  const logout = (event) => {
    event.preventDefault();
    Auth.logout();
  };

  const [anchorEl, setAnchorEl] = React.useState(null);
  const [anchorAvatarEl, setAnchorAvatarEl] = React.useState(null);

  const open = Boolean(anchorEl);
  const avatarOpen = Boolean(anchorAvatarEl);

  const handleClick = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleAvatarClick = (event) => setAnchorAvatarEl(event.currentTarget);
  const handleAvatarClose = () => setAnchorAvatarEl(null);

  return (
    <motion.div initial={{ y: -50, opacity: 0 }} animate={{ y: 0, opacity: 1 }} transition={{ duration: 0.5 }}>
      <AppBar position="static" sx={{ bgcolor: green[700] }}>
        <Toolbar sx={{ display: "flex", justifyContent: "space-between" }}>
          {/* Left Section - Brand */}
          <Typography
            component={Link}
            to="/"
            variant="h5"
            sx={{
              textDecoration: "none",
              fontWeight: "bold",
              color: "white",
              cursor: "pointer",
            }}
          >
            Green Thumb Intelligence
          </Typography>

          {/* Center Section - Navigation */}
          <Box sx={{ display: "flex", gap: 3 }}>
            <motion.div whileHover={{ scale: 1.1 }} whileTap={{ scale: 0.95 }}>
              <Typography sx={{ color: "white", cursor: "pointer" }} onClick={handleClick}>
                <Grid container alignItems="center" spacing={1}>
                  Community
                  <KeyboardArrowDownIcon />
                </Grid>
              </Typography>
            </motion.div>

            <Menu
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              PaperProps={{
                elevation: 5,
                sx: {
                  mt: 1.5,
                  "&:before": {
                    content: '""',
                    display: "block",
                    position: "absolute",
                    top: 0,
                    right: 14,
                    width: 10,
                    height: 10,
                    bgcolor: "background.paper",
                    transform: "translateY(-50%) rotate(45deg)",
                    zIndex: 0,
                  },
                },
              }}
            >
              <MenuItem component={Link} to="/forum">Forum</MenuItem>
              <MenuItem component={Link} to="/people">People</MenuItem>
              <Divider />
              <MenuItem component={Link} to="/about">About</MenuItem>
            </Menu>
          </Box>

          {/* Right Section - Theme Toggle, Login/Profile */}
          <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
            <FormControlLabel
              sx={{ color: "white" }}
              control={
                <Switch color="default" onChange={themeToggle} />
              }
              label={theme === "light" ? "Light Mode" : "Dark Mode"}
              labelPlacement="start"
            />

            {Auth.loggedIn() ? (
              <>
                <motion.div whileHover={{ scale: 1.2 }}>
                  <Avatar
                    onClick={handleAvatarClick}
                    sx={{
                      cursor: "pointer",
                      bgcolor: "white",
                      color: "green",
                    }}
                  >
                    <PersonIcon />
                  </Avatar>
                </motion.div>

                <Menu
                  anchorEl={anchorAvatarEl}
                  open={avatarOpen}
                  onClose={handleAvatarClose}
                  PaperProps={{
                    elevation: 5,
                    sx: {
                      mt: 1.5,
                      "&:before": {
                        content: '""',
                        display: "block",
                        position: "absolute",
                        top: 0,
                        right: 14,
                        width: 10,
                        height: 10,
                        bgcolor: "background.paper",
                        transform: "translateY(-50%) rotate(45deg)",
                        zIndex: 0,
                      },
                    },
                  }}
                >
                  <MenuItem component={Link} to="/profile">Profile</MenuItem>
                  <MenuItem onClick={logout} component={Link} to="/">Logout</MenuItem>
                </Menu>
              </>
            ) : (
              <>
                <motion.div whileHover={{ scale: 1.1 }}>
                  <Button
                    component={Link}
                    to="/login"
                    sx={{
                      color: "green",
                      bgcolor: "white",
                      ":hover": {
                        bgcolor: "green",
                        color: "white",
                      },
                    }}
                  >
                    Login
                  </Button>
                </motion.div>

                <motion.div whileHover={{ scale: 1.1 }}>
                  <Button
                    component={Link}
                    to="/signup"
                    sx={{
                      color: "green",
                      bgcolor: "white",
                      ":hover": {
                        bgcolor: "green",
                        color: "white",
                      },
                    }}
                  >
                    Sign Up
                  </Button>
                </motion.div>
              </>
            )}
          </Box>
        </Toolbar>
      </AppBar>
    </motion.div>
  );
};

export default Header;
