import * as React from 'react';
import {
  Grid,
  Card,
  CardMedia,
  CardContent,
  Typography,
  CardActions,
  IconButton,
  Chip,
  useMediaQuery,
  ListSubheader,
} from '@mui/material';
import InfoIcon from '@mui/icons-material/Info';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
import AddPlantDialog from '../AddPlantDialog';

export default function Garden({ plants, user }) {
  const [open, setOpen] = React.useState(false);
  const isSmallScreen = useMediaQuery('(max-width:600px)');

  const handleClickOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <Grid
      container
      spacing={3}
      sx={{
        px: 3,
        py: 2,
        overflowY: 'auto',
        height: 'calc(100vh - 100px)',
      }}
    >
      {/* Header */}
      <Grid item xs={12}>
        <ListSubheader
          component="div"
          sx={{ fontSize: '24px', display: 'flex', alignItems: 'center' }}
        >
          {user}'s Garden
          <Chip
            sx={{
              fontSize: '11px',
              width: 100,
              height: 28,
              cursor: 'pointer',
              ml: 2,
              ':hover': { borderColor: 'green' },
            }}
            icon={<AddIcon />}
            onClick={handleClickOpen}
            label="Add Plant"
            variant="outlined"
          />
        </ListSubheader>
        <AddPlantDialog open={open} handleClose={handleClose} />
      </Grid>

      {/* Plant Cards */}
      {plants && plants.length > 0 ? (
        plants.map((plant) => (
          <Grid item xs={12} sm={6} md={4} key={plant._id}>
            <Card
              sx={{
                borderRadius: '12px',
                boxShadow: 3,
                transition: 'transform 0.3s',
                '&:hover': {
                  transform: 'scale(1.02)',
                },
              }}
            >
              <CardMedia
                component="img"
                height="200"
                image={`${plant.image_path}?w=500&fit=crop&auto=format`}
                srcSet={`${plant.image_path}?w=500&fit=crop&auto=format&dpr=2 2x`}
                alt={plant.common_name}
                loading="lazy"
              />
              <CardContent>
                <Typography variant="h6" component="div">
                  {plant.common_name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  <i>{plant.scientific_name}</i>
                </Typography>
              </CardContent>
              <CardActions disableSpacing>
                <IconButton
                  aria-label={`info about ${plant.common_name}`}
                  component={Link}
                  to={`/plant/${plant._id}`}
                  sx={{ ml: 'auto', color: 'primary.main' }}
                >
                  <InfoIcon />
                </IconButton>
              </CardActions>
            </Card>
          </Grid>
        ))
      ) : (
        <Grid item xs={12}>
          <Typography variant="body1" sx={{ p: 2 }}>
            No plants to show.
          </Typography>
        </Grid>
      )}
    </Grid>
  );
}
